 .card-stack > .card:hover {
    border: 1px solid lightgray;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s; 
    -webkit-transition-duration: all 0.5s; 
         -o-transition-duration: all 0.5s; 
            transition-duration: all 0.5s;
    -webkit-transform: translateY(-25%);
        -ms-transform: translateY(-25%);
            transform: translateY(-25%);
  }

  .card-stack > .card:active {
    background: rgb(154, 255, 255);
  } 

  .card-stack >.card:active {
    -webkit-transform: scale(4);
        -ms-transform: scale(4);
            transform: scale(4);
    -webkit-transition: 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
    -o-transition: 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
    -webkit-transform-origin: bottom;
        -ms-transform-origin: bottom;
            transform-origin: bottom;
  }


